body {
    font-family: Lato
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    font-family: Montserrat
}

hr.star-dark,
hr.star-light,
hr.star-white {
    max-width: 15rem;
    padding: 0;
    text-align: center;
    border: none;
    border-top: .25rem solid;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem
}

hr.star-dark:after,
hr.star-light:after,
hr.star-white:after {
    position: relative;
    top: -.8em;
    display: inline-block;
    padding: 0 .25em;
    content: "\f005";
    font-family: FontAwesome;
    font-size: 2em
}

hr.star-white {
    border-color: #ffffff
}

hr.star-white:after {
    color: #ffffff;
    background-color: #000000
}

hr.star-light {
    border-color: #fff
}

hr.star-light:after {
    color: #fff;
    background-color: #18bc9c
}

hr.star-dark {
    border-color: #2c3e50
}

hr.star-dark:after {
    color: #2c3e50;
    background-color: #fff
}

section {
    padding: 6rem 0
}

section h2 {
    font-size: 2.25rem;
    line-height: 2rem
}

@media (min-width:992px) {
    section h2 {
        font-size: 3rem;
        line-height: 2.5rem
    }
}

.btn-xl {
    padding: 1rem 1.75rem;
    font-size: 1.25rem
}

.btn-social {
    width: 3.25rem;
    height: 3.25rem;
    font-size: 1.25rem;
    line-height: 2rem
}

.scroll-to-top {
    z-index: 1042;
    right: 1rem;
    bottom: 1rem;
    display: none
}

.scroll-to-top a {
    width: 3.5rem;
    height: 3.5rem;
    background-color: rgba(33, 37, 41, .5);
    line-height: 3.1rem
}

#mainNav {
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-weight: 700;
    font-family: Montserrat
}

#mainNav .navbar-brand {
    color: #fff
}

#mainNav .navbar-nav {
    margin-top: 1rem;
    letter-spacing: .0625rem
}

#mainNav .navbar-nav li.nav-item a.nav-link {
    color: #fff
}

#mainNav .navbar-nav li.nav-item a.nav-link:hover {
    color: #18bc9c
}

#mainNav .navbar-nav li.nav-item a.nav-link:active,
#mainNav .navbar-nav li.nav-item a.nav-link:focus {
    color: #fff
}

#mainNav .navbar-nav li.nav-item a.nav-link.active {
    color: #18bc9c
}

#mainNav .navbar-toggler {
    font-size: 80%;
    padding: .8rem
}

@media (min-width:992px) {
    #mainNav {
        padding-top: 1.5rem;
        padding-bottom: 1.5rem;
        transition: padding-top .3s, padding-bottom .3s
    }
    #mainNav .navbar-brand {
        font-size: 2em;
        transition: font-size .3s
    }
    #mainNav .navbar-nav {
        margin-top: 0
    }
